* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  overflow-x: hidden;
}
body > *::-webkit-scrollbar {
  display: none;
}
body::-webkit-scrollbar {
  width: 5px;
  background-color: lightgray;
}
body::-webkit-scrollbar-thumb {
  background-color: gray;
  border-radius: 20px;
}
